}

@media screen and (min-width: 768px) {
  .button::before {
    background-color: #fff;
    position: absolute;
    top: 0;
    left: 0;
    content: "";
    width: 100%;
    height: 100%;
    transform: scale(0, 1);
    transform-origin: left top;
    transition: 0.3s cubic-bezier(0.45, 0, 0.55, 1);
    z-index: -1;
  }
}

@media screen and (min-width: 768px) {
  .button.button--reverse::before {
    background-color: #2689d3;
  }
}

@media screen and (min-width: 768px) {
  .button:hover:before {
    transform: scale(1, 1);
  }
}

