@media screen and (min-width: 768px) {
  .voice-card__image {
    max-width: 11.25rem;
    aspect-ratio: 180/140;
  }
}

.voice-card__body {
  grid-row: 2;
  grid-column: 1/3;
}
.voice-card__text {
  overflow: hidden;
  display: -webkit-box;
  -webkit-box-orient: vertical;
  -webkit-line-clamp: 7; /* 任意の行数を指定 */
}

.voice-cards {
  display: grid;
  row-gap: 2.5rem;
}
@media screen and (min-width: 768px) {
  .voice-cards {
    grid-template-columns: repeat(2, 1fr);
    column-gap: 2.5rem;
    row-gap: 5rem;
  }
}

.voice__inner {
  position: relative;
  max-width: 37.5rem;
}

@media screen and (min-width: 768px) {
  .voice__inner::before {
    background-image: url(../images/illust-fish2.png);
    background-size: contain;
    background-position: center top;
    background-repeat: no-repeat;
    position: absolute;
    content: "";
    left: calc(50% - min(48vw, 38.75rem));
    top: -7.8125rem;
    width: 18.8125rem;
    height: 8.625rem;
    z-index: 1;
  }
}

@media screen and (min-width: 768px) {
  .voice__inner::after {
    background-image: url(../images/illust-seahorse.png);
    background-size: contain;
    background-position: center top;
    background-repeat: no-repeat;
    position: absolute;
    content: "";
    right: 1.5625rem;
    bottom: -3.8125rem;
    width: 4.4375rem;
    height: 10.125rem;
    z-index: 1;
  }
}

.voice__heading {
  margin-top: -0.3125rem;
}

.voice__cards {
  margin-top: 2.5rem;
}
@media screen and (min-width: 768px) {
  .voice__cards {
    margin-top: 1.875rem;
  }
}

.voice__button {
  margin-top: 2.3125rem;
  text-align: center;
}
@media screen and (min-width: 768px) {
  .voice__button {
    margin-top: 5rem;
  }
}

